import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_sn-chart-screen-reader-table.scss';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const SnChartScreenReaderTable = (state) => {
	// chart options
	const {options = {}, data} = state.properties;
	const {accessibilityTableCaption = ''} = options;
	const {header, body} = data;

	return (
		<table className="sn-chart-screen-reader-table">
			<caption>{accessibilityTableCaption}</caption>
			<thead>
				<tr>
					<td />
					{header.slice(1).map((cell) => {
						return (
							<th scope="col" data-test="name">
								<div className="now-line-height-crop">{cell}</div>
							</th>
						);
					})}
				</tr>
			</thead>
			<tbody>
				{body.map((dataRow) => {
					return (
						<tr>
							<th>
								<div className="now-line-height-crop">{dataRow[0]}</div>
							</th>
							{dataRow.slice(1).map((dataPoint) => {
								return (
									<td>
										<div className="now-line-height-crop">{dataPoint}</div>
									</td>
								);
							})}
						</tr>
					);
				})}
			</tbody>
		</table>
	);
};

/**
 * @seismicElement sn-chart-screen-reader-table
 */
createEnhancedElement('sn-chart-screen-reader-table', {
	properties: {
		/**
		 * screen reader table options
		 * @type {Object}
		 */
		options: {schema: {type: 'object'}},
		/**
		 * `flatTable` export of data from the chart.
		 * @type {Object}
		 */
		data: {schema: {type: 'object'}}
	},
	view: SnChartScreenReaderTable,
	styles
});
